import React, { Component } from 'react';
import { connect } from 'react-redux';
import 'tachyons';
import DoneOutlineIcon from '@material-ui/icons/DoneOutline';

import ToolTip from './ToolTip';
import copy from '../../assets/Images/copy.png';
import { BASE_URL, USER_DETAILS } from '../../constants/otherConstants';

window.Clipboard = (function (window, document, navigator) {
  let textArea;
  function isOS() {
    return navigator.userAgent.match(/ipad|iphone/i);
  }
  function createTextArea(text) {
    textArea = document.createElement('textArea');
    textArea.value = text;
    document.body.appendChild(textArea);
  }

  function selectText() {
    let range;
    let selection;

    if (isOS()) {
      range = document.createRange();
      range.selectNodeContents(textArea);
      selection = window.getSelection();
      selection.removeAllRanges();
      selection.addRange(range);
      textArea.setSelectionRange(0, 999999);
    } else {
      textArea.select();
    }
  }

  function copyToClipboard() {
    document.execCommand('copy');
    document.body.removeChild(textArea);
  }

  const copy = (text) => {
    createTextArea(text);
    selectText();
    copyToClipboard();
  };

  return {
    copy,
  };
}(window, document, navigator)
);

class AdminPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      copySuccess: false,
      user: {},

    };
    this.links = [];
    this.location = '';
  }

  componentWillMount() {
    // console.log(this.props);
    if(this.props.user && this.props.user.user){
        this.setState({user:this.props.user.user})
    }
  }

  componentWillReceiveProps(np) {
    if (np.user) {
      if (this.state.user !== np.user.user) {
        this.setState({ user: np.user.user });
      }
    }
  }


  copyCodeToClipboard = () => {
    // navigator.clipboard.writeText();
    window.Clipboard.copy(`${window.location.origin}${BASE_URL}/${this.state.user.username}`);
    this.setState({ copySuccess: true });
    setTimeout(() => this.setState({ copySuccess: false }), 1000);
  }


  render() {
    const { username } = this.state.user;

    return (
      <div>
        <ToolTip innertext="Add this Sharable link to your bio">
          <div className="  bg-white w-100  center pt2  flex tc justify-center bb">

            <p className="  center mt0 mb0  block flex-wrap  " style={{ overflowY: 'auto' }}>
                        Your Website Link
              <br />

              <a className="blue pointer  grow" ref={(linkref) => this.linkref = linkref} target="_blank" href={`${window.location.origin}${BASE_URL}/${this.state.user.username}`}>{`${window.location.origin}${BASE_URL}/${username}`}</a>
            </p>

            <button className="pointer grow  bg-blue white bl b flex items-center ma2 pa2 br2 " style={{ border: 'none' }} type="button" onClick={() => this.copyCodeToClipboard()}>
              <img width={25} src={copy} />
            Copy
            </button>
          </div>

        </ToolTip>
        {
        this.state.copySuccess
          ? (
            <div className="copied-succes-div">
              <DoneOutlineIcon />
              <p>
          Copied!!!
              </p>
            </div>
          ) : null
      }
      </div>


    );
  }
}


const mapStateToProps = (state) => ({
  user: state.AuthReducer.user,
  links: state.AdminPageReducers.links,
  profileViews: state.AdminPageReducers.profileViews,

});

const mapDispatchToProps = (dispatch) => ({

});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(AdminPage);
