/* eslint-disable camelcase */
import React, { Component } from 'react';
import { connect } from 'react-redux';
import 'tachyons';
import './styles.css';
import {

} from '../../../constants/actionTypes';
import CreateProduct from './CreateProduct';
import Image from '../../../common/Components/Image';

class AdminPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      product: { },
      user: {},

    };
  }

  componentWillMount() {
    const { data, user } = this.props;
    if (data) {
      if (user.user) {
        if (data.user === user.user.id) this.setState({ product: data, isResellingProduct: false });
        else this.setState({ product: data, isResellingProduct: true });
      }
      // this.setState({ product: data });
    }
    if (user && user.user) {
      this.setState({ user: user.user });
    }
  }

  componentWillReceiveProps(np) {
    const { user, product } = this.state;
    if (np.data && np.data !== product) {
      if (np.user.user) {
        if (np.data.user === np.user.user.id) this.setState({ product: np.data, isResellingProduct: false });
        else this.setState({ product: np.data, isResellingProduct: true });
      }
    }
    if (np.user && np.user.user && np.user.user !== user) {
      this.setState({ user: np.user.user });
    }
  }

  render() {
    const { isResellingProduct, product } = this.state;
    const {
      name, disc_price, price, images, preview_images, resell_margin,
    } = product;

    return (
      <div className="admin-product relative" style={this.props.style}>
        {
          isResellingProduct
            ? (
              <div className="resell-div">
                {`Your margin ${resell_margin} Rs.`}
              </div>
            )
            : null
        }
        <div className="product-header-img" style={{ overflow: 'hidden', position: 'relative' }}>
          {
                images.length > 0
                  ? <Image src={images[0]} alt="" overlaySrc={preview_images[0]} />
                  : <img src="" alt="" />
            }
        </div>
        <div className="product-pricing">
          <h3>
            {name}
          </h3>
          {
            disc_price === price
              ? (
                <b>{`Rs. ${price}`}</b>
              )
              : (
                <div>
                  <b>{`Rs. ${disc_price}`}</b>
                  <strike>{` Rs. ${price}`}</strike>
                </div>
              )
          }
        </div>

        <CreateProduct data={product} update isResellingProduct={isResellingProduct}/>

      </div>

    );
  }
}


const mapStateToProps = (state) => ({
  user: state.AuthReducer.user,

});

const mapDispatchToProps = (dispatch) => ({

});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(AdminPage);
